@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  color: #111 !important;
  background-color: #FEFEFE;
  font-weight: normal;
}

.main-body {
  height: 100%;
}

.header {
  padding: 10px 0px;
  background-color: #FFF;
  width: 100vw;
}

h1 {
  padding-left: 30px;
  font-family: 'Roboto', sans-serif;
}

hr {
  margin-top:-10px;
}

h2 {
  color: #2F4F4D;
}

.nav-bar {
  display: flex;
  background-color: #A9DBD7;
  border-top: 1px solid #87ADAA;
  border-bottom: 1px solid #87ADAA;
  margin: 0px 0px 15px 0px;
  box-shadow: 0px 5px 10px #EEE;
}

.nav-bar a {
  text-decoration: none;
  color: #111;
  padding: 10px;
}

.scrollButton {
  background-color: #507A77;
  color: #FFF;
  border: 1px solid #2F4F4D;
  padding: 8px;
  font-size: 14px;
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 15px;
  cursor: pointer;
}

.tab {
  width: 7%;
  height: 100%;
  padding: 8px;
  text-align: center;
  background-color: #A9DBD7;
  border-bottom: 2px solid transparent;
}

.tab:hover {
  background-color: #8FBDB9;
  border-bottom:2px solid #507A77;
}

.content {
  padding: 0px 20px;
  height: 100%;
}

.section {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-items: center;
}

.s1-image {
  padding: 10px 30px 10px 10px;
}

.s1-image img {
  width: 250px;
  border: 4px solid #000;
}

.s2-image {
  padding: 10px 10px 10px 30px;
}

.s2-image img {
  width: 300px;
  border: 4px solid #000;
}

/* images.js */

.img-container {
  margin-top: 40px;
  display: grid;
  justify-items: center;
  justify-content: center;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(200px, 400px));
}

.img-child {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.caption {
  padding: 10px 0px;
  text-align: center;
}

.img-child img {
  width: 350px;
  border: 4px solid #000;
  cursor: pointer;
}

/* lightbox */

.lightbox {
  display: grid;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0,0.8);
  z-index: 1;
  overflow: auto;
  position: fixed;
  align-content: center;
  justify-content: center;
}

.lightbox-content {
  width: 85%;
  margin: auto;
  max-width: 750px;
  border: 5px solid #FFF;
}

/* videos.html */

.vid-container {
  margin-top: 40px;
  display: grid;
  justify-items: center;
  justify-content: center;
  grid-gap: 3rem;
  grid-template-columns: repeat(auto-fit, minmax(200px, 560px));
}

.vid-child {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 560px;
}

.vid-caption {
  background-color: #BFD6D4;
  padding: 15px;
  margin-top: 10px;
  border: 2px solid #507A77;
  border-radius: 10px;
}

.vid-child iframe {
  width: 100%;
}

/* projects.html */

.proj-image {
  width: 125px;
  padding: 10px 30px 10px 10px;
}

.section a {
  color: #507A77;
  font-style: italic;
}

/* form */
#form-section {
  padding: 10px;
  display: flex;
  justify-items: center;
  height: 100%;
}

.form {
  width: 50%;
  height: 67vh;
  padding: 0px 16px 16px 16px;
  margin-right: 12px;
  margin-bottom: 12px;
  overflow-y: auto;
  border-radius: 8px;
}

.responses {
  width: 50%;
  height: 67vh;
  margin-left: 12px;
  margin-bottom: 12px;
  overflow-y: auto;
}

.response {
  background-color: #BFD6D4;
  padding: 15px;
  margin-top: 10px;
  border: 2px solid #507A77;
  line-height: 25px;
}

#submit {
  background-color: #507A77;
  color: #FFF;
  border: 1px solid #2F4F4D;
  padding: 5px;
  font-size: 14px;
  cursor: pointer;
}

input[type=text] {
  border: 1px solid #AAA;
  border-radius: 4px;
  padding: 4px 8px;
  margin: 4px 0px 0px 0px;
  width: 100%;
  box-sizing: border-box;
}

textarea {
  border: 1px solid #AAA;
  border-radius: 4px;
  padding: 4px 8px;
  margin: 4px 0px 0px 0px;
  width: 100%;
  box-sizing: border-box;
}

select {
  width: 100%;
}

b {
  color: #B00000;
  font-weight: normal;
}

.note {
  float: right;
  font-size: 14px;
}

.name {
  color: #2F4F4D;
  font-size: 18px;
  font-weight: bold;
  margin: 8px 8px 0px 0px;
  line-height: 25px;
}

.message {
  margin: 16px 0px 0px 8px;
}

.date {
  float: right;
  font-size: 14px;
  letter-spacing: 1.5px;
}

/* movies.js */

.mov-container {
  margin-top: 0px;
  display: grid;
  justify-items: center;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(150px, 300px));
  grid-gap: 4px;
}

.mov-child {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.movCaption {
  padding: 10px 0px;
  text-align: center;
}

.mov-child img {
  width: 250px;
  border: 4px solid #000;
  cursor: pointer;
}

#list {
  background-color: #507A77;
  color: #FFF;
  border: 1px solid #2F4F4D;
  padding: 2px 8px;
  font-size: 14px;
  cursor: pointer;
}

#top-bar {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
  background-color: #FEFEFE;
}

#search-bar {
  width: 300px;
}

#search {
  width: 245px;
}

#search-btn {
  margin: 4px 0px;
  padding: 4px 4px;
  float: right;
  background-color: #507A77;
  color: #FFF;
  border: 1px solid #2F4F4D;
  cursor: pointer;
}

#add-list {
  background-color: #FFF;
  color: #333;
  border: 1px solid #507A77;
  padding: 0px 8px;
  font-size: 14px;
  width: 150px;
  cursor: pointer;
  float: left;
}

#list-btn {
  background-color: #507A77;
  color: #FFF;
  border: 1px solid #2F4F4D;
  padding: 2px 8px;
  font-size: 14px;
  margin: 8px 0px 0px 4px;
  float: left;
  cursor: pointer;
}

#list-container {
  width: 200px;
  margin-top: 5px;
  float: right;
}

#delButton {
  background-color: #B00000;
  color: #FFF;
  float: right;
  margin: 8px 0px 0px 4px;
  padding: 2px 8px;
  font-size: 14px;
}

#pag-container {
  width: 100%;
  margin: 20px 0px 0px 0px;
  text-align: center;
}

#pagination {
  background-color: #507A77;
  color: #FFF;
  border: 1px solid #2F4F4D;
  padding: 8px;
  font-size: 14px;
  margin: 0 auto;
  cursor: pointer;
}

/* movie lightbox */

.movLightbox {
  display: grid;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0,0.8);
  z-index: 1;
  overflow: auto;
  position: fixed;
  align-content: center;
  justify-content: center;
}

.movLightbox-container {
  width: 750px;
  margin: auto;
  max-width: 850px;
  background-color: #FFF;
  border: 5px solid #FFF;
  border-radius: 10px;
  box-shadow: 0px 0px 8px #000;
  display: flex;
  flex-direction: row;
}

.movLightbox-content {
  height: 100%;
  margin: auto;
  float: left;
  border: 5px solid #FFF;
  outline: 1px solid #111;
  outline-offset: -5px;
}

.title {
  color: #2F4F4D;
  font-weight: bold;
  font-size: 36px;
  line-height: 48px;
}

.director {
  font-size: 24px;
  line-height: 32px;
}

.rating {
  background-color: #EBBA34;
  border: 1px solid #BD8A00;
  color: #222;
  padding: 8px 24px 8px 24px;
  font-weight: bold;
}

.movLightbox-info {
  height: 100%;
  background-color: #FFF;
  margin: auto;
  padding: 0px 20px;
  float: right;
  max-width: 850px;
  color: #555;
}

/* Add.js */

#add-box {
  margin: 30px;
  width: 700px;
}

@media only screen and (max-width: 750px){
  .rating { font-size: 12px; }
  #delButton { float: left; }
  .lightbox-content { width: 90%; }
  .movLightbox-container { width: 500px; }
  .movLightbox-content { width: 250px; }
  .director {
    font-size: 18px;
    line-height: 28px;
  }
  .title {
    color: #2F4F4D;
    font-weight: bold;
    font-size: 28px;
    line-height: 36px;
  }
  .section {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
  }
  .nav-bar {
    display: flex;
    flex-direction: column;
    background-color: #A9DBD7;
    border-top: 1px solid #87ADAA;
    border-bottom: 1px solid #87ADAA;
    margin: 0 auto;
    box-shadow: 0px 5px 10px #EEE;
  }
  .tab {
    width: 100%;
    height: 100%;
    text-align: center;
    border-bottom: 2px solid transparent;
  }
  #add-box {
    width: 90%;
  }
}